  componentDidMount() {
    this.setState(
      {
        strChoose: <HorizontalLabel imageFunctions={this.state.images1} />,
      });

    new Promise(resolve => {
      this.state.three.initThree();
      this.state.three.outDoorScene();
      this.state.three.createCss();
      this.state.three.SpriteName("spriteOutDoor",false);
      this.state.three.createSprite(this.state.spriteOutDoor);
      this.state.three.createSprite(this.state.spriteInSide);
      this.state.three.controlChange();
      resolve()
    }).then(() => {
      this.state.three.LoadInsideTexture('./static/panorama/black/', 'jpg')
      this.state.three.LoadInsideTexture('./static/panorama/darkgrey/', 'jpg')

    })


  }


  componentWillUnmount() {
    this.state.three.Dispose()
  }



