ThreejsAPI.prototype.initThree = function (id) {

    // let grid;

    init();
    animate();
    window.scene = scene
    window.THREE = THREE
    window.THREE.$$RendererList = [];
    window.THREE.$$RendererList.push(renderer);

    function init() {
        container = document.getElementById('threejs');
        renderer = new THREE.WebGLRenderer({
            antialias: true
        });
        renderer.antialias = true; // 抗锯齿
        // renderer.autoClear = true; // 自动清除
        renderer.setPixelRatio(window.devicePixelRatio); //像素比 一般都是这个
        renderer.outputEncoding = THREE.LinearFilter
        renderer.textureEncoding = THREE.sRGBEncoding
        //renderer.setSize(width, height);
        renderer.setSize(window.innerWidth, window.innerHeight);
        //document.getElementById('canvas-frame').appendChild(renderer.domElement); //canvas-frame元素中插入canvas对象
        renderer.setClearColor(0xFFFFFF,
            1.0
        ); //renderer.setClearColor(0xEEEEEE, 0.0);或者renderer.setClearAlpha(0.0);它的意思是每次绘制清除缓冲区后的颜色通道设置,简单就是设置每次绘制的背景颜色
        renderer.toneMapping = THREE.ACESFilmicToneMapping; //色彩鲜明 色彩不鲜明
        renderer.toneMappingExposure = 1.0;
        // renderer.setClearAlpha(0.8); //setClearAlpha值，为背景透明度
        // renderer.shadowMap.enabled = true; //3-4 point
        // renderer.shadowMap.type = THREE.PCFSoftShadowMap; //阴影都是马赛克
        // renderer.gammaInput = true;
        // renderer.gammaOutput = true; //更鲜艳






        document.body.appendChild(renderer.domElement);
        scene = new THREE.Scene();
        camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.1, 200000); //pc70 
        // scene.fog = new THREE.Fog( 0xeeeeee, 1000, 3000 );

        controls = new OrbitControls(camera, container);

        controls.enablePan = false;
        // controls.minDistance
        controls.enableDamping = true;
        controls.minDistance = 200;
        controls.enableZoom = true;
        controls.maxDistance = 800;
        // controls.maxDistance = 350;
        controls.update();


        // grid = new THREE.GridHelper( 40, 40, 0x000000, 0x000000 );
        // grid.scale.set(100,100,100)
        // grid.material.opacity = 0.1;
        // grid.material.depthWrite = false;
        // grid.material.transparent = true;
        // scene.add( grid );



        var light = new THREE.AmbientLight(0x46464646);
        light.intensity = 3;
        scene.add(light);
        // scene.background = new THREE.Color(0x000000); //迷雾 这样设置就会导致你设置渲染alpha的透明度没作用,

        const hemiLight = new THREE.HemisphereLight(0xffffff, 0x000000, 1);
        hemiLight.intensity = 5;
        hemiLight.position.set(0, 100, 0);
        scene.add(hemiLight);



        
        const pointLight = new THREE.PointLight(0xffffff); //点光没阴影 聚光有
        pointLight.position.set(-190, 35, 70);
        scene.add(pointLight);
		// const spotLight = new THREE.SpotLight( 0xffffff,10000 );
		// spotLight.position.set( -202, 41, 70 );
		// scene.add( spotLight );
        // spotLight.target.position.set(-1000,-1000,-1000)
        // scene.add(light.target);




		// const spotLightHelper = new THREE.SpotLightHelper( spotLight );
		// scene.add( spotLightHelper );

        // spotLight.target.updateMatrixWorld();
        // spotLightHelper.update();

        // var axis = new THREE.AxesHelper(10000);
        // scene.add(axis)



        window.addEventListener('resize', onWindowResize, false);

    }





    function onWindowResize() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(window.innerWidth, window.innerHeight);
        if (labelRenderer !== null && labelRenderer !== undefined) {
            labelRenderer.setSize(window.innerWidth, window.innerHeight);
        }
    }

    function animate() {
        
		const time = - performance.now() / 1000;
        // console.log(camera.position)
        requestAnimationFrame(animate);
        controls.update(); // required when damping is enabled
        // TWEEN.update()
        renderer.render(scene, camera);
        if (labelRenderer !== null && labelRenderer !== undefined) {
            labelRenderer.render(scene, camera);
        }
        // grid.position.z = - ( time ) % 5;
        // console.log(camera.position)

    }

    
}



