ThreejsAPI.prototype.GoInside = function () {
    camera.position.set(0, 0, 0.01)
    camera.lookAt(scene.position)
}

ThreejsAPI.prototype.LoadInsideTexture = function (strParentPath, strSuffix) {

    var materials = [];
    if (mapAlltexture.has(strParentPath + strSuffix)) {
        materials = mapAlltexture.get(strParentPath + strSuffix)
    } else {
        var pngArr = ['r', 'l', 'u', 'd', 'f', 'b'];
        const textures = [];
        for (let index = 0; index < pngArr.length; index++) {
            const element = pngArr[index];
            textures.push(new THREE.TextureLoader().load(strParentPath + element + '.' + strSuffix)) //'./AboutCar/panorama/Lightgrey/'
        }
        for (let i = 0; i < 6; i++) {
            materials.push(new THREE.MeshBasicMaterial({
                map: textures[i],
                transparent: true,
            }));
        }
        mapAlltexture.set(strParentPath + strSuffix, materials)
    }


}



//室内
ThreejsAPI.prototype.skyboxAdd = function (strParentPath, strSuffix) {

    controls.rotateSpeed = -0.5;
    controls.minPolarAngle = 0.0;
    controls.maxPolarAngle =3.0; //3.14 / 2.1;
    renderer.toneMappingExposure = 1.0;
    controls.enableZoom = false;
    controls.minDistance = 0;
    controls.maxDistance = 800;
    let materials

    if (mapAlltexture.has(strParentPath + strSuffix)) {
        materials = mapAlltexture.get(strParentPath + strSuffix)
    }
    // debugger
    if (skyBox !== null && skyBox !== undefined) {
        skyBox.material = materials
    } else {
        skyBox = new THREE.Mesh(new THREE.BoxBufferGeometry(10, 10, 10), materials);
        skyBox.geometry.scale(1, 1, -1);
        scene.add(skyBox);
    }


}


